  display: flex;
}
@media (max-width: 1199px) {
  .about__container {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .about__container {
    padding: 0;
  }
}

.about__title-wrapper {
  max-width: 355px;
  margin-left: auto;
  margin-right: 88px;
}
@media (max-width: 1199px) {
  .about__title-wrapper {
    min-width: 503px;
    text-align: center;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .about__title-wrapper {
    text-align: left;
    min-width: 100%;
    padding: 0 15px;
  }
}

.about__media {
  min-width: 722px;
  position: relative;
}
@media (max-width: 1199px) {
  .about__media {
    min-width: 100%;
    position: static;
  }
}
@media (max-width: 767px) {
  .about__media {
    display: flex;
    flex-direction: column;
    row-gap: 40px;
  }
}

.about__player-wrapper {
  position: absolute;
  overflow: hidden;
  bottom: -121px;
  left: -66px;
  z-index: 1;
}
@media (max-width: 1199px) {
  .about__player-wrapper {
    bottom: 113px;
    left: calc(50% - 133px);
  }
}
@media (max-width: 767px) {
  .about__player-wrapper {
    position: relative;
    bottom: 0;
    left: 0;
    order: 2;
  }
}
.about__player-wrapper .about__cover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  content: url("../img/media-layer.png");
}
@media (max-width: 1199px) {
  .about__player-wrapper .about__cover {
    content: url("../img/media-layer-tablet.png");
  }
}
@media (max-width: 767px) {
  .about__player-wrapper .about__cover {
    content: url("../img/media-layer-mobile.png");
    width: 100%;
  }
}
.about__player-wrapper iframe {
  width: 364px;
  height: 228px;
}
@media (max-width: 1199px) {
  .about__player-wrapper iframe {
    width: 267px;
    height: 167px;
  }
}
@media (max-width: 767px) {
  .about__player-wrapper iframe {
    width: 100%;
    min-height: 160px;
    aspect-ratio: 1/0.5;
    height: 100%;
  }
}

.about__foto-wrapper {
  display: flex;
  justify-content: flex-start;
}
@media (max-width: 1199px) {
  .about__foto-wrapper {
    justify-content: center;
    position: relative;
  }
}
@media (max-width: 767px) {
  .about__foto-wrapper {
    flex-direction: column;
  }
}
@media (max-width: 1199px) {
  .about__foto-wrapper img {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .about__foto-wrapper img {
    min-height: 313px;
  }
}

.footer {
  background-color: #030a1a;
}

